<template>
  <VaCard class="flex flex-col">
    <VaCardTitle class="flex items-start justify-between">
      <h1 class="card-title text-secondary font-bold uppercase">Annual Queues Report</h1>
      <div class="flex gap-2">
        <VaButton class="h-2" size="small" preset="primary" @click="exportAsCSV">Export</VaButton>
      </div>
    </VaCardTitle>
    <VaCardContent class="flex flex-col-reverse md:flex-row md:items-center justify-between gap-5 h-full">
      <QueueReportChart
        class="w-full h-full min-h-72 sm:min-h-32 pt-4"
        :queues="queues"
        :months="months"
        v-if="queues.length"
      />
    </VaCardContent>
  </VaCard>
</template>

<script lang="ts" setup>
import { ref, computed, onMounted } from 'vue'
import { VaCard } from 'vuestic-ui'
import QueueReportChart from './QueueReportChart.vue'
import { downloadAsCSV } from '../../../../services/toCSV'
import {
  months,
  getQueues,
} from '../../../../data/charts/queueChartData'
const queues = ref([])

const currentYear = new Date().getFullYear()

onMounted(async () => {
  queues.value = await getQueues(currentYear, months)
})

const monthsWithCurrentYear = months.map((month) => `${month} ${currentYear}`)


const selectedMonth = ref(monthsWithCurrentYear[0])

const exportAsCSV = () => {
  downloadAsCSV(queues.value, 'queue-report')
}
</script>
